@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Sans:ital,wght@0,100..800;1,100..800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
}
#root{
  min-height: 100vh;
  display: flex;
  flex-direction: column; 
  justify-content: space-between;
}

body {
  font-family: "Ubuntu Sans", sans-serif;
  margin: 0;
  padding: 0;
  background-color: #F5EE84;
  color: #072AC8;
}

.App {
  text-align: center;
}
footer{
  padding-top: 15px;
  background-color: #F5EE84;
  height: 50px;
  width: 100%;
  text-align: center;
  position: fixed;
  top: 95%;
  
}
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    background-color: #F5EE84;

}

.logo {
    display: flex;
    justify-content: flex-start;
}

.nav-center {
    display: flex;
    align-items: center;
    text-align: center;
    list-style: none;
    padding: 0px;
    grid-gap: 40px;
    gap: 40px;

}

.nav-center li a {
    color: #072AC8;
    font-weight: bold;
    text-decoration: none;
    text-align: center;
}

.social-links {
    display: flex;
    justify-content: flex-end;
    grid-gap: 10px;
    gap: 10px;
    flex: 1 1;
}

.menu-toggle {
    display: none;
}

.nav-center-mobile {
    display: none; 
    color: #072AC8;
    font-weight: bold;
    list-style: none;
    text-decoration: none;
}

@media (max-width: 768px) {
    header {
        flex-direction: column;
        align-items: flex-start;

        z-index: 1;
        background-color: #F5EE84;
    }

    .nav-center {
        display: none;
    }

    .menu-toggle {
        display: block;
        cursor: pointer;
        position: fixed;
        top: 20px;
        right: 20px;
        z-index: 9999;
    }

    .menu-toggle .bar {
        width: 35px;
        height: 5px;
        background-color: rgba(0, 0, 0, 0.9);
        margin: 6px 0;
        transition: 0.4s;
    }

    .open .bar:nth-child(1) {
        transform: rotate(-45deg) translate(-9px, 10px);
    }

    .open .bar:nth-child(2) {
        opacity: 0;
    }

    .open .bar:nth-child(3) {
        transform: rotate(45deg) translate(-9px, -10px);
    }

    .nav-links {
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100%;
        background-color: #F5EE84;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        grid-gap: 20px;
        gap: 20px;
        transform: translateY(-100%);
        transition: transform 0.3s ease-in-out;
    }

    .nav-links.open {
        transform: translateY(0);
    }

    .nav-center-mobile {
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 25px;
        gap: 25px;
        margin-top: 60%;
        text-align: center;
        padding: 0px;
    }

    .social-links {
        justify-content: center;
        margin-top: 20px;
    }
}

@media (min-width: 769px) {
    .nav-center-mobile {
        display: none;
    }

    .nav-center {
        display: flex;
    }

    .menu-toggle {
        display: none;
    }
}

.hero {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 60px 40px;
    flex-wrap: wrap;
    margin-top: 50px;
}

.intro {
    max-width: 500px;
    margin-left: 100px;
}

.intro p {
    font-size: 22px;
    margin: 10px 0;
}

.intro h1 {
    font-size: 48px;
    margin: 10px 0;
}

button {
    padding: 10px 20px;
    font-size: 18px;
    background-color: #FFE156;
    border: none;
    cursor: pointer;
    color: #072AC8;
    font-weight: bold;
}

.photo {
    
    flex-shrink: 0;
}

.photo img {
    margin-right: 50px;
    border-radius: 50%;
    width: 300px;
    height: 300px;
    object-fit: cover;
    border: 5px solid #fff3b5;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
 
}

.hero button{
    border:2px solid rgb(255, 245, 169);
}

@media (max-width: 768px){
 .intro{
    margin: 0px ;
    
 }   

 .photo img {
    margin: 10px;
 }

}
section {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.parent-skill {
    width: 200px;
    height: auto;
    position: relative; 
}

.skill {
    margin-top: 50px;
    width: 200px;
    height: 200px;
    border-radius: 100%;
    box-shadow: 10px 10px 19px #d3d041, -10px -10px 19px #f8ff91;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer; 
}

.progress {
    width: 97%;
    height: 97%;
    border-radius: 100%;
    background: linear-gradient(280deg, #f0e000, #ffe48d);
    display: flex;
    align-items: center;
    justify-content: center;
}

.progress-number {
    font-family: 'Poppins', sans-serif;
    color: #0400ff;
    font-size: 2rem;
}

.title {
    font-family: 'Poppins', sans-serif;
    color: #3700ff;
    font-size: 2rem;
    display: block;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 100px;
}

.emoji {
    font-size: 2rem;
    position: absolute;
    animation: fly 3s ease-out forwards;
    opacity: 0;
}

@keyframes fly {
    0% {
        top: 50%;
        left: 50%;
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }
    100% {
        top: calc(50% - 200px + (400px * var(--direction-y)));
        left: calc(50% - 200px + (400px * var(--direction-x)));
        opacity: 0;
        transform: translate(-50%, -50%) scale(0);
    }
}

@media (max-width: 768px) {
    section {
        flex-direction: column;
        align-items: center;
    }

    .parent-skill {
        padding: 0px;
        margin: 0px;
    }
}

.work img{
    width: 35%;
    position: relative;
    margin: 35px;
    border: 2px solid black;
    border-radius: 5px;
}
.work{
    margin-bottom: 100px;

}


@media (max-width: 768px) {
    .work img {
        width: 350px;
        margin: 20px;
      
    }

    .work{
        padding: 0px;
        margin: 0px;
    
    }
}

.about {
    padding: 2em;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}

.text {
    position: relative;
    width: 100%;
    height: 400px; /* Adjust the height as needed */

    overflow: hidden;
    padding: 10px;

    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;

}

.draggable-word {
    margin: 0 0.2em;
    padding: 0.2em;
    border: 1px solid #ccc;
    cursor: move;
    -webkit-user-select: none;
            user-select: none;

    font-size: 18px; /* Increase font size */
    position: absolute;
}
@media (max-width: 768px) {

    .draggable-word{
        border: 1px solid rgba(0, 0, 0, 0);
        font-size: 20px;
        margin: 0px;
        padding: 0px;
    }
    }




.container {
    max-width: 1000px;
    margin-bottom: 50px;

    display: flex;

}

.section-block {
    flex: 1 1;
    margin: 50px 25px;
}

.section-info {
    color: #3700ff;
    font-size: 25px;
}

.section-info h2 {
    margin: 0 0 10px 0;
}

.section-info p {
    opacity: 0.9;
    margin-bottom: 15px;
}

.social-links a {
    color: #232F31;
    font-size: 36px;
    text-decoration: none;
    opacity: 0.9;
}

.social-links a:hover {
    opacity: 1;
}

.form-block {
    color: #3700ff;
}

.form-block .form-label {
    margin-bottom: 12px;
    font-size: 18px;
    font-weight: bold;
}

.form-block input[type=text],
.form-block textarea {
    width: 100%;
    height: 30px;
    border-radius: 5px;
    background: #495558;
    border: none;
    margin-bottom: 6px;
    color: rgba(255, 255, 255, 0.9);
    font-size: 16px;
    text-indent: 8px;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.4);
}

.form-block input[type=text]:focus,
.form-block textarea:focus {
    outline: none;
    box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.3);
}

.form-block textarea {
    padding-top: 5px;
    height: 120px;
}

.form-block input[type=submit] {
    font-size: 16px;
    margin-top: 10px;
    background: #232F31;
    border: none;
    color: rgba(255, 255, 255, 0.9);
    padding: 5px 12px;
    border-radius: 3px;
    font-weight: lighter;
    opacity: 0.9;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.4);
}

.form-block input[type=submit]:focus {
    outline: none;
    opacity: 1;
}

.form-block input[type=submit]:hover {
    opacity: 1;
}

@media (max-width: 920px) {
    .container {
        flex-direction: column;
    }

    .section-block:nth-child(1) {
        margin-bottom: 0;
    }
}

